import React from 'react';
import CustomBodyOfMonth from "../CustomBodyOfMonth";


const Months = (props) => {
  const {classes, table} = props;

  const getMonthsDays = (days) => (
    days.map((day) => {
      return (
        <CustomBodyOfMonth key={day.day} {...day} />
      )
    })
  );

  const getMonths = (months) => (
    months.map((month) => (
      <li key={month.id}>
        <div id={month.id} name={month.name}>
          <div className={classes.monthName}>{month.name}</div>
          {getMonthsDays(month.days)}
        </div>
      </li>
    ))
  );

  return (
    <ul>
      {getMonths(table)}
    </ul>
  )
};

Months.defaultProps = {};

Months.displayName = 'Months';


export default Months;

